@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed.woff2") format("woff2"), url("../fonts/roboto-condensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PT-Mono_Regular.woff2") format("woff2"), url("../fonts/PT-Mono_Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 15px;
  color: #f2f5f7;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: inherit;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  cursor: pointer;
  box-sizing: border-box;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn--info {
  padding: 15px 25px;
  background-color: #00b2ff;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
}
@media (max-width: 767px) {
  .btn--info {
    font-size: 14px;
    line-height: 1;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #f2f5f7;
  padding-top: 30px;
  padding-bottom: 35px;
  position: fixed;
}
@media (max-width: 1023px) {
  .main-header {
    padding-top: 30px;
    padding-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}

.main-header__logo {
  margin-right: auto;
}

.main-header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1222px;
}

.main-header__button {
  margin-left: auto;
}
@media (max-width: 1023px) {
  .main-header__button {
    display: none;
  }
}

.logo {
  display: flex;
  gap: 15px;
}

@media (max-width: 767px) {
  .logo__image {
    width: 35px;
    height: 35px;
  }
}

@media (max-width: 767px) {
  .logo__content {
    display: none;
  }
}

.logo__title {
  margin: 0;
  margin-bottom: -5px;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
}

.logo__description {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7);
}

.main-nav__list {
  list-style: none;
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.main-nav__link {
  color: #f2f5f7;
  display: flex;
  align-items: center;
  cursor: pointer;
  box-sizing: border-box;
  transition: opacity 0.3s ease;
}
.main-nav__link:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.8;
}

.main-nav__image {
  margin-right: 15px;
}

@media (max-width: 1023px) {
  .main-nav__content {
    display: none;
  }
}

.info {
  padding-top: 305px;
  padding-bottom: 280px;
  background-color: #000000;
  background-image: url("../../img/background-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
}
@media (max-width: 1023px) {
  .info {
    padding-top: 180px;
    padding-bottom: 205px;
  }
}
@media (max-width: 767px) {
  .info {
    padding-top: 150px;
    padding-bottom: 190px;
  }
}

.info__container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 1222px;
  margin-left: 125px;
  margin-right: 125px;
}
@media (max-width: 1023px) {
  .info__container {
    margin-left: 0;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .info__container {
    margin-left: 0;
    margin-right: 0;
  }
}

.info__title {
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f7;
  margin: 0;
  margin-bottom: 40px;
}
@media (max-width: 1023px) {
  .info__title {
    font-size: 64px;
    line-height: 1;
  }
}
@media (max-width: 767px) {
  .info__title {
    font-size: 36px;
    line-height: 1;
  }
}

.info__description {
  font-family: "PT Mono", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 155%;
  color: #f2f5f7;
  margin: 0;
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .info__description {
    font-size: 14px;
    line-height: 1.55;
  }
}

.features-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 30px;
  max-width: 1222px;
  margin: 0 auto;
  list-style: none;
  min-width: 270px;
}
@media (max-width: 767px) {
  .features-list {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
  }
}

.list {
  margin: 0;
  list-style: none;
}

.feature {
  padding: 40px 45px 35px;
  display: flex;
  flex-direction: column;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  min-height: 300px;
}
@media (hover: hover) {
  .feature:hover:not(.focus-visible) {
    background-color: #00b2ff;
    color: #f2f5f7;
    border: none;
  }
  .feature:hover:not(.focus-visible) .feature__image,
.feature:hover:not(.focus-visible) .feature__title {
    color: #f2f5f7;
  }
}
.feature.focus-visible:focus {
  background-color: #00b2ff;
  color: #f2f5f7;
  border: none;
}
.feature.focus-visible:focus .feature__image,
.feature.focus-visible:focus .feature__title {
  color: #f2f5f7;
}

.feature__image {
  margin-bottom: 20px;
}

.feature__title {
  margin: 0;
  margin-bottom: 20px;
}

.feature__image,
.feature__title {
  color: #00b2ff;
}

.features {
  padding-top: 120px;
  padding-bottom: 60px;
}
@media (max-width: 1023px) {
  .features {
    padding-top: 80px;
    padding-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .features {
    padding-top: 50px;
    padding-bottom: 25px;
  }
}

