.logo {
  display: flex;
  gap: 15px;
}

.logo__image {
  @include vp-767 {
    width: 35px;
    height: 35px;
  }
}

.logo__content {
  @include vp-767 {
    display: none;
  }
}

.logo__title {
  margin: 0;
  margin-bottom: -5px;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
}

.logo__description {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: rgba(255, 255, 255, 0.7);
}
